
#app{
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    max-width: 450px;
}
#app input{
    -ms-flex: 1;
        flex: 1;
    padding: 7px 8px;
    border: 1px solid #ccc;
    background: #fff;
    font-size: 13px;
    line-height: 13px;
    outline: 0;
}
#app button{
    display: inline-block;
    border: 1px solid #ccc;
    border-left: 0;
    background-color: #eee;
    background-image: linear-gradient(#fcfcfc, #eee);
}



